import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#root',
  router,
  components: { App },
  template: '<App/>',
  created: function(){
	var currentUrl = window.location.pathname;
    console.log(currentUrl);
  }
})




